import Vue from 'vue'
import Vuex from 'vuex'

Vue.use(Vuex)

const store = new Vuex.Store({
    state: {
        dataShow: [],
        dataAdded: [],
        businessValue: ''
    },
    mutations: {
        changeDataShow(state, value) {
            state.dataShow = value;
        },
        changeBusinessValue(state, value) {
            state.businessValue = value;
        },
        getAllData(state) {
            var url = `/show`;
            axios.get(url).then(res => {
                state.dataShow = res.data;
                console.log(this.dataShow);
            })
        },
        deleteData(state, dataDelete) {
            dataDelete.forEach(data => {
                state.dataShow.forEach((item, index, arr) => {
                    if (item === data) {
                        arr.splice(index, 1);
                    }
                })
            })
        },
        deleteDataAdded(state, dataDelete) {
            dataDelete.forEach(data => {
                state.dataAdded.forEach((item, index, arr) => {
                    if (item === data) {
                        arr.splice(index, 1);
                    }
                })
            })
        }
    }
})
export default store


