<template>
  <div class="selectorvue">
   <div>筛选数据</div>
   <div>
       <span>业务类型</span>
       <el-select v-model="businessValue" placeholder="业务类型" class="dataselect">
       <el-option
       v-for="item in businessOptions"
       :key="item.label"
       :label="item.label"
       :value="item.value"
       >
       </el-option>
       </el-select>
   </div>
   <div>
      <span>材料名称</span>
      <el-select v-model="nameIndex" placeholder="请选择" class="dataselect"  @change="changeName">
        <el-option
        v-for="(item,index) in nameOptions"
        :key="index"
        :label="item.label"
        :value="index">
        </el-option>
      </el-select>
   </div>
   <div>
      <span>材料型号</span>
      <el-select v-model="type" placeholder="请选择" class="dataselect">
        <el-option
        v-for="(item,index) in nameOptions[nameIndex].type"
        :key="index"
        :label="item"
        :value="item">
      </el-option>
    </el-select>
   </div>

  <div>
      <span>入库日期</span>
      <el-date-picker
      v-model="inputDay"
      type="daterange"
      align="right"
      unlink-panels
      range-separator="至"
      start-placeholder="记录开始日期"
      end-placeholder="记录结束日期"
      :picker-options="pickerOptions"
      value-format="yyyy-MM-dd"
      class="dataselect">
      </el-date-picker>
  </div>
  <div>
    <span>使用日期</span>
    <el-date-picker
      v-model="date"
      type="daterange"
      align="right"
      unlink-panels
      range-separator="至"
      start-placeholder="使用开始日期"
      end-placeholder="使用结束日期"
      :picker-options="pickerOptions"
      class="dataselect"
      value-format="yyyy-MM-dd">
    </el-date-picker>
  </div>

     <el-button type="primary" round @click="selectData()">搜索</el-button>
  </div>
</template>

<script>
export default {
  name: 'selectorvue',
  data () {
    return {
      pickerOptions: {
          shortcuts: [{
            text: '最近一周',
            onClick(picker) {
              const end = new Date();
              const start = new Date();
              start.setTime(start.getTime() - 3600 * 1000 * 24 * 7);
              picker.$emit('pick', [start, end]);
            }
          }, {
            text: '最近一个月',
            onClick(picker) {
              const end = new Date();
              const start = new Date();
              start.setTime(start.getTime() - 3600 * 1000 * 24 * 30);
              picker.$emit('pick', [start, end]);
            }
          }, {
            text: '最近三个月',
            onClick(picker) {
              const end = new Date();
              const start = new Date();
              start.setTime(start.getTime() - 3600 * 1000 * 24 * 90);
              picker.$emit('pick', [start, end]);
            }
          }]
      },
      date: '',
      inputDay: '',
      nameOptions: [{
            label:"所有材料",
            type:["所有型号",
                    "DN25"
                    ,"DN32"
                    ,"DN40"
                    ,"DN50"]
            },{
            label: 'PE给水管',
            type:["所有型号",
                    "DN25"
                    ,"DN32"
                    ,"DN40"
                    ,"DN50"]
            }, {
            label: 'PE直接',
            type:["所有型号","DN25"
                    ,"DN32"
                    ,"DN40"
                    ,"DN50"
                    ,"DN32*25"
                    ,"异经40*25"
                    ,"异经40*32"
                    ,"DN50*32"
                    ,"DN63*32"
                    ,"DN50*40"
                    ,"DN63*40"
                    ,"DN63"
                    ,"DN50"]
            }, {
            label: '水表',
            type:["所有型号",'DN20']
            }, {
            label: '加密阀',
            type:["所有型号",'DN20']
            }, {
            label: '软密封闸阀',
            type:["所有型号",'DN20'
                    ,'DN25'
                    ,'DN32'
                    ,'DN150'
                    ,'DN200'
                    ,'DN300'
                    ,'DN50'
                    ,'DN65'
                    ,'DN80'
                    ,'DN40'
                    ,'DN100']
            },{
            label: 'PE弯头',
            type:["所有型号",'DN25'
                    ,'DN32'
                    ,'DN40'
                    ,'DN50']
            }, {
            label: 'PE三通',
            type:["所有型号","DN25"
                    ,"DN32"
                    ,"DN40*25"
                    ,"DN32*25"
                    ,"DN50*40"]
            }, {
            label: '管帽',
            type:["所有型号",'DN25',
                    'DN32']
            }, {
            label: '焊刚管',
            type:["所有型号",'DN40*1.5'
                    ,'DN50*1.5']
            }, 
            {
            label: 'PE外丝直接',
            type:["所有型号","DN25*3/4"
                    ,"DN32"
                    ,"DN40"
                    ,"DN50"
                    ,"DN63"]
            },
            {
            label: 'PE内丝直接',
            type:["所有型号","DN25*3/4",
                    "DN25*1/2","DN50"]
            },
            {
            label: '钢钉卡',
            type:["所有型号",'DN32'
                    ,'DN25']
            }, {
            label: '井盖井圈',
            type:["所有型号",'重700Q']
            }, {
            label: '生料带',
            type:[]
            }],
      nameIndex: 0,
      type:'',
      businessOptions:[
            {
              value:'materialInfor',
              label:'维修',
            },
            {
              value:'installInfor',
              label:'安装'
                
            },
            {
              value:'receiveInfor',
              label:'领材料',
            },
            {
              value:'sumInfor',
              label:'库存',
            },
      ],
    }
  },
  methods:{    
    alertMessage(message,type) {
        this.$message({
              showClose: true,
              message:message,
              type: type,
              offset:10,
              duration:1000
            });
      },
    changeName(){
        this.type = ''
    },
    selectData(){
      if (!this.businessValue) {
        this.alertMessage('请选择业务类型','warning')
        return
      }
      console.log(this.businessValue);
      console.log('搜索');
       var url = `/selector?businessValue=${this.businessValue}`;
       if(this.nameOptions[this.nameIndex].label!=="所有材料"){
         url+=`&materialName=${this.nameOptions[this.nameIndex].label}`
       }
       if(this.type!=="所有型号"){
         url+=`&materialType=${this.type}`
       }
       if(this.date&&this.businessValue!='sumInfor'){
         url+=`&dateStart=${this.date[0]}&dateEnd=${this.date[1]}`
       }
      if(this.inputDay&&this.businessValue!='sumInfor'){
         url+=`&inputDayStart=${this.inputDay[0]}&inputDayEnd=${this.inputDay[1]}`
       }
       axios.get(url).then(res=>{
         this.dataShow = res.data
         console.log(this.dataShow);
       })
    },
  },
  computed:{
    dataShow:{
      get(){
        return this.$store.state.dataShow
      },
      set(value){
        this.$store.commit('changeDataShow', value)
      }
    },
    businessValue:{
      get(){
        return this.$store.state.businessValue
      },
      set(value){
        this.$store.commit('changeBusinessValue', value)
      }
    }
  },
}
</script>


<style scoped>
.selectorvue{
display: inline-block;
}
.dataselect{
width: 280px;
margin-top: 10px;
margin-bottom: 10px;
margin-right: 15px;
}
</style>



