<template>
  <div class="showAllVue">
    <div class="inforBox">
        <div class="title">
          材料记录
        </div>
        <el-table
            :data="dataShow"
            style="width: 100%"
            height=550
            border
            @selection-change="test">>
            <el-table-column
            type="selection"
            align="center"
            width="60">
            </el-table-column>
            <el-table-column
            fixed
            prop="materialName"
            label="材料名称"
            align="center"
            width="100">
            </el-table-column>
            <el-table-column
            prop="materialType"
            label="材料规格"
            align="center"
            width="100">
            </el-table-column>
            <el-table-column
            prop="num"
            label="数量"
            align="center"
            width="50">
            </el-table-column>
            <el-table-column
            prop="dayString"
            label="日期"
            align="center"
            width="100">
            </el-table-column>
            <el-table-column
            prop="inputDayString"
            label="录入日期"
            align="center"
            width="100">
            </el-table-column>
            <el-table-column
            prop="infor"
            label="备注"
            align="left"
            width="300">
            </el-table-column>
        </el-table>
        <div class="btn"> <el-button type="success" round @click='deleteData()'>删除数据</el-button></div>
    </div>
    <selectorVue class="selector" />
  </div>
</template>

<script>
import selectorVue from "./selector.vue";

export default {
  name: 'showAllVue',
  data () {
    return {
        multipleSelection: [],
        isEdit:false,
    }
  },
  components:{
      selectorVue,
  },
  mounted(){

  },
  methods:{
    test(val){
        this.multipleSelection = val;
    },
    edit(data){
      console.log(data);
      this.ChangeEditState()
    },
    ChangeEditState(){
      this.isEdit = !this.isEdit
    },
    alertMessage(message,type) {
        this.$message({
              showClose: true,
              message:message,
              type: type,
              offset:10,
              duration:1000
            });
      },
    deleteData(){
        console.log('删除成功');
        if (this.businessValue=='sumInfor') {
             this.alertMessage('库存数据不能删除','warning')
             return
        }
        this.multipleSelection.forEach(data => {
            this.deleteApi(data.id)
            this.sumInforUpdate(data)
        });
        this.$store.commit('deleteData',this.multipleSelection)
        console.log(this.dataShow);
    },
    deleteApi(id){
        var url = `/delete?businessValue=${this.businessValue}&id=${id}`;
        axios.get(url).then(res=>{
        })
    },
    showAllData(){
        console.log('拿到数据成功');
        var url = `/show`;
        axios.get(url).then(res=>{
            this.dataShow = res.data;
            // console.log(this.dataShow);
        })
        .catch((err)=>{
          console.log(err);
        })
    },
    sumInforUpdate(inforObj){
         var bValue=''
         if (this.businessValue =='materialInfor') {
            bValue='receiveInfor'
         }else if(this.businessValue =='receiveInfor'){
            bValue='materialInfor'
         }
         var url = `/sumInforUpdate?businessValue=${bValue}&materialName=${inforObj.materialName}&materialType=${inforObj.materialType}&inputDay=${inforObj.inputDayString}&day=${inforObj.dayString}&num=${inforObj.num}`;
         axios.get(url).then(res=>{
             console.log(res);
         })
         .catch(()=>{
             console.log(err);
         })
    },
  }, 
  computed:{
    dataShow:{
      get(){
        return this.$store.state.dataShow
      },
      set(value){
        this.$store.commit('changeDataShow', value)
      }
    },
    businessValue:{
      get(){
        return this.$store.state.businessValue
      },
      set(value){
        this.$store.commit('changeBusinessValue', value)
      }
    }
  },
}
</script>


<style scoped>
.title{
    width: 150px;
    margin: 0 auto;
}


.inforBox{
    width: 68%;
    position: absolute;
    left: 10px;
    }
.selector{
    width: 30%;
    height: 580px;
    position: absolute;
    right: 10px;
    display: inline-block;
    border-radius: 10px;
}   

</style>



