<template>
  <div class="summaryTable">
    <div class="timeSelector">
       <span>业务类型</span>
       <el-select v-model="businessValue" placeholder="业务类型" class="dataselect">
       <el-option
       v-for="item in businessOptions"
       :key="item.label"
       :label="item.label"
       :value="item.value"
       >
       </el-option>
       </el-select>
        <span>入库日期</span>
        <el-date-picker
        v-model="inputDay"
        type="daterange"
        align="right"
        unlink-panels
        range-separator="至"
        start-placeholder="记录开始日期"
        end-placeholder="记录结束日期"
        :picker-options="pickerOptions"
        value-format="yyyy-MM-dd"
        class="dataselect">
        </el-date-picker>
        <span>日期</span>
        <el-date-picker
        v-model="repairDay"
        type="daterange"
        align="right"
        unlink-panels
        range-separator="至"
        start-placeholder="使用开始日期"
        end-placeholder="使用结束日期"
        :picker-options="pickerOptions"
        class="dataselect"
        value-format="yyyy-MM-dd">
        </el-date-picker>
        <el-button type="primary" round @click="selectData()">生成报表</el-button>
    </div>
    <div class="inforBox">
        <el-table
            :data="dataSummaryTableNew"
            style="width: 100%"
            height=550
            border
            :default-sort="{prop: 'numSum', order: 'descending'}"
            >
            <el-table-column
            type="selection"
            align="center"
            width="50">
            </el-table-column>
            <el-table-column
            fixed
            prop="materialName"
            label="材料名称"
            align="center"
            width="250">
            </el-table-column>
            <el-table-column
            prop="materialType"
            label="材料规格"
            align="center"
            width="250">
            </el-table-column>
            <el-table-column
            prop="numSum"
            label="总数量"
            align="center"
            width="250">
            </el-table-column>
        </el-table>
    </div>
  </div>
</template>

<script>
export default {
  name: 'summaryTable',
  data () {
    return {
        dataSummaryTable:[],
        dataSummaryTableNew:[],
        dataSelected:[],
        repairDay:'',
        inputDay:'',
        pickerOptions: {
          shortcuts: [{
            text: '最近一周',
            onClick(picker) {
              const end = new Date();
              const start = new Date();
              start.setTime(start.getTime() - 3600 * 1000 * 24 * 7);
              picker.$emit('pick', [start, end]);
            }
          }, {
            text: '最近一个月',
            onClick(picker) {
              const end = new Date();
              const start = new Date();
              start.setTime(start.getTime() - 3600 * 1000 * 24 * 30);
              picker.$emit('pick', [start, end]);
            }
          }, {
            text: '最近三个月',
            onClick(picker) {
              const end = new Date();
              const start = new Date();
              start.setTime(start.getTime() - 3600 * 1000 * 24 * 90);
              picker.$emit('pick', [start, end]);
            }
          }]
        },
        materialOptions: [
            {
            label: 'PE给水管',
            value: 'PE给水管',
            children:[{
                value: 'DN25',
                label: 'DN25',
                },{
                value: 'DN32',
                label: 'DN32',
                },{
                value: 'DN40',
                label: 'DN40',
                },{
                value: 'DN50',
                label: 'DN50',
                }]
            }, {
            label: 'PE直接',
            value: 'PE直接',
            children:[{
                value: 'DN25',
                label: 'DN25',
                },{
                value: 'DN32',
                label: 'DN32',
                },{
                value: 'DN40',
                label: 'DN40',
                },{
                value: 'DN50',
                label: 'DN50',
                },{
                value: 'DN32*25',
                label: 'DN32*25',
                },{
                value: '异经40*25',
                label: '异经40*25',
                },{
                value: '异经40*32',
                label: '异经40*32',
                },{
                value: 'DN50*32',
                label: 'DN50*32',
                },{
                value: 'DN63*32',
                label: 'DN63*32',
                },{
                value: 'DN50*40',
                label: 'DN50*40',
                },{
                value: 'DN63*40',
                label: 'DN63*40',
                },{
                value: 'DN63',
                label: 'DN63',
                },
                {
                value: 'DN50',
                label: 'DN50',
                }]
            }, {
            label: '水表',
            value: '水表',
            children:[{
                value: 'DN20',
                label: 'DN20',
                }]
            }, {
            label: '加密阀',
            value: '加密阀',
            children:[{
                value: 'DN20',
                label: 'DN20',
                }]
            }, {
            label: '软密封闸阀',
            value: '软密封闸阀',
            children:[{
                value: 'DN20',
                label: 'DN20',
                },{
                value: 'DN25',
                label: 'DN25',
                },{
                value: 'DN32',
                label: 'DN32',
                },{
                value: 'DN150',
                label: 'DN150',
                },{
                value: 'DN200',
                label: 'DN200',
                },{
                value: 'DN300',
                label: 'DN300',
                },{
                value: 'DN50',
                label: 'DN50',
                },{
                value: 'DN65',
                label: 'DN65',
                },{
                value: 'DN80',
                label: 'DN80',
                },{
                value: 'DN40',
                label: 'DN40',
                },{
                value: 'DN100',
                label: 'DN100',
                }
            ]
            },{
            label: 'PE弯头',
            value: 'PE弯头',
            children:[{
                value: 'DN25',
                label: 'DN25',
                },{
                value: 'DN32',
                label: 'DN32',
                },{
                value: 'DN40',
                label: 'DN40',
                },{
                value: 'DN50',
                label: 'DN50',
                }]
            }, {
            label: 'PE三通',
            value: 'PE三通',
            children:[
                {
                value: 'DN25',
                label: 'DN25',
                },{
                value: 'DN32',
                label: 'DN32',
                },{
                value: 'DN40*25',
                label: 'DN40*25',
                },{
                value: 'DN32*25',
                label: 'DN32*25',
                },{
                value: 'DN50*40',
                label: 'DN50*40',
                }
            ]
            }, {
            label: '管帽',
            value: '管帽',
            children:[{
                value: 'DN25',
                label: 'DN25',
                },{
                value: 'DN32',
                label: 'DN32',
                }]
            }, {
            label: '焊刚管',
            value: '焊刚管',
            children:[
                {
                value: 'DN40*1.5',
                label: 'DN40*1.5',
                },{
                value: 'DN50*1.5',
                label: 'DN50*1.5',
                }
            ]
            }, 
            {
            label: 'PE外丝直接',
            value: 'PE外丝直接',
            children:[
                {
                value: 'DN25*3/4',
                label: 'DN25*3/4',
                },{
                value: 'DN32',
                label: 'DN32',
                },{
                value: 'DN40',
                label: 'DN40',
                },{
                value: 'DN50',
                label: 'DN50',
                },{
                value: 'DN60',
                label: 'DN60',
                }
            ]
            },
            {
            label: 'PE内丝直接',
            value: 'PE内丝直接',
            children:[
                {
                value: 'DN25*3/4',
                label: 'DN25*3/4',
                },{
                value: 'DN25*1/2',
                label: 'DN25*1/2',
                },{
                value: 'DN50',
                label: 'DN50',
                }
            ]
            },
            {
            label: '钢钉卡',
            value: '钢钉卡',
            children:[
                {
                value: 'DN25',
                label: 'DN25',
                },{
                value: 'DN32',
                label: 'DN32',
                }
            ]
            }, {
            label: '井盖井圈',
            value: '井盖井圈',
            children:[
                {
                value: '重700Q',
                label: '重700Q',
                }
            ]
            }, {
            label: '生料带',
            value: '生料带',
            children:[
                {
                    value:'生料带',
                    label:'生料带',
                }
            ]
            }
        ],
        businessOptions:[
            {
              value:'materialInfor',
              label:'维修',
            },
            {
              value:'installInfor',
              label:'安装'
                
            },
            {
              value:'receiveInfor',
              label:'领材料',
            },
            {
              value:'sumInfor',
              label:'库存',
            },
        ],
        businessValue:''
    }
  },
  methods:{
    addNum(n1, n2) {
      //解决浮点数相加的问题
            var s1, s2, m;
            try {
                s1 = n1.toString().split(".")[1].length;
            } catch (e) {
                s1 = 0;
            }
            try {
                s2 = n2.toString().split(".")[1].length;
            } catch (e) {
                s2 = 0;
            }
            m = Math.pow(10, Math.max(s1, s2));
            return (n1 * m + n2 * m) / m;
    },
    alertMessage(message,type) {
      this.$message({
          showClose: true,
          message:message,
          type: type,
          offset:10,
          duration:1000
          });
    },
    selectData(){
      console.log('搜索');
      console.log(this.businessValue);
      if (!this.businessValue) {
        this.alertMessage('请选择业务类型','warning')
        return
      }
       var url = `/selector?businessValue=${this.businessValue}`;
       if(this.repairDay){
         url+=`&dateStart=${this.repairDay[0]}&dateEnd=${this.repairDay[1]}`
       }
      if(this.inputDay){
         url+=`&inputDayStart=${this.inputDay[0]}&inputDayEnd=${this.inputDay[1]}`
       }
       axios.get(url).then(res=>{
            this.dataSelected= res.data
            console.log(this.dataSelected);
            console.log(this.dataSelected);
            this.generateSummaryTable()
       })

    },
    generateSummaryTable(){
        this.dataSummaryTable=[]
        var dataSummaryObj={}
        var i = 0;
        this.materialOptions.forEach((material)=>{
            material.children.forEach((type)=>{
                this.dataSummaryTable[i]={materialName:material.value,
                    materialType:type.value,
                    numSum:0
                };
                this.dataSelected.forEach(data => {
                    if (data.materialName==material.value&&data.materialType==type.value) {
                        // this.dataSummaryTable[i].numSum+=parseFloat(data.num)
                        this.dataSummaryTable[i].numSum = this.addNum(this.dataSummaryTable[i].numSum,parseFloat(data.num))
                    }
                });
                i+=1;
            })
        })
        this.dataSummaryTableNew = this.dataSummaryTable
    }
  },
  computed:{
  },
}
</script>


<style scoped>
.summaryTable{
    position: relative;
}
.inforBox{
    margin:0 auto;
    width: 805px;
    height: 70%;
}
.timeSelector{
    margin-bottom: 20px;
}
</style>



