import Vue from 'vue'
import Router from 'vue-router'
// import interfaceVue from '@/components/interfaceVue'
import inputVue from '@/components/inputVue'
import showAllVue from '@/components/showAllVue'
import summaryTable from '@/components/summaryTable'
Vue.use(Router)

export default new Router({
    routes: [{
            path: '/inputVue',
            name: 'inputVue',
            component: inputVue
        },
        {
            path: '/showAllVue',
            name: 'showAllVue',
            component: showAllVue
        },
        {
            path: '/summaryTable',
            name: 'summaryTable',
            component: summaryTable
        },
        {
            path: '*',
            name: 'inputVue',
            component: inputVue
        },
    ]
})


