                label: '重700Q',
                }
            ]
            }, {
            label: '生料带',
            value: '生料带',
            children:[
                {
                    value:'生料带',
                    label:'生料带',
                }
            ]
            }
        ],
        material:[],
        num:'',
        manName:'',
        pickerOptions: {
          disabledDate(time) {
            return time.getTime() > Date.now();
          },
          shortcuts: [{
            text: '今天',
            onClick(picker) {
              picker.$emit('pick', new Date());
            }
          }, {
            text: '昨天',
            onClick(picker) {
              const date = new Date();
              date.setTime(date.getTime() - 3600 * 1000 * 24);
              picker.$emit('pick', date);
            }
          }, {
            text: '一周前',
            onClick(picker) {
              const date = new Date();
              date.setTime(date.getTime() - 3600 * 1000 * 24 * 7);
              picker.$emit('pick', date);
            }
          }]
        },
        repairDay:'',
        inputDay:'',
        infor:'',
        inforAdded:[],
        mainId:'',
        businessOptions:[
            {
              value:'materialInfor',
              label:'维修',
            },
            {
              value:'installInfor',
              label:'安装'
                
            },
            {
              value:'receiveInfor',
              label:'领材料',
            },
      ],
        businessValue:''
    }
  },
  methods:{
    materialInforAdding(){
        this.inputDay = this.getTodayDate();
        var inforObj={
            address:this.address[0]+this.address[1],
            manName:this.manName,
            materialName:this.material[0],
            materialType:this.material[1],
            num:this.num,
            repairDay:this.repairDay,
            inputDay:this.inputDay,
            infor:this.infor
        }
        this.inforAdded.push(inforObj)
        console.log(this.inforAdded);
    },
    selectAddedInfor(val){
        this.multipleSelection = val;
    },
    delectSelectedAddInfor(){
        this.multipleSelection.forEach(data => {
            this.inforAdded.forEach((item, index, arr) => {
                if (item === data) {
                    arr.splice(index, 1);
                }
            })
        })
        console.log(this.inforAdded);
    },
    restart(){
        this.address=['','']
        this.manName=''
        this.material=[]
        this.inforAdded = []
        this.infor =''
        this.repairDay='';
        this.num = ''
    },
    checkAndAdd() {
        if(!this.material[0]||!this.material[0]){
            this.alertMessage('请选择材料','warning')
            return 
        }
        if(!this.repairDay){
            this.alertMessage('请选择时间','warning')
            return 
        }
        this.alertMessage('添加数据数据成功','success')
        this.materialInforAdding()
    },
    alertMessage(message,type) {
        this.$message({
            showClose: true,
            message:message,
            type: type,
            offset:10,
            duration:1000
            });
      },
    checkAndCommit() {
        if(!this.repairDay){
            this.alertMessage('请选择时间','warning')
            return 
        }
        if(!this.inforAdded.length){
            this.alertMessage('请添加材料','warning')
            return 
        }
        this.alertMessage('提交数据成功','success')
        if (this.businessValue==='materialInfor') {
            this.repairDateInput()
            return
 
        }
        if (this.businessValue==='installInfor') {
            this.installDateInput()
 
        }
        if (this.businessValue==='receiveInfor') {
            this.receiveDateInput()
        }
        this.material=''
        this.num = ''
        this.inforAdded=[]
    },
    receiveDateInput(){
        this.inforAdded.forEach((inforObj)=>{
            var url = `/receiveInforInput?materialName=${inforObj.materialName}&materialType=${inforObj.materialType}&inputDay=${inforObj.inputDay}&day=${inforObj.repairDay}&num=${inforObj.num}`;
            axios.get(url).then(res=>{
                console.log(res);
            })
            .catch(()=>{
                console.log(err);
            })
            this.sumInforUpdate(inforObj)
        })
    },
    sumInforInput(){
        var i = 0
        this.materialOptions.forEach((material)=>{
            material.children.forEach((type)=>{
                var inforObj = {
                    materialName:material.value,
                    materialType:type.value,
                    inputDay:'2020-08-01',
                    repairDay:'2020-08-01',
                    num:0
                }
                // console.log(material.value+type.value);
                i++
                console.log(i);
                var url = `/sumInforInput?materialName=${inforObj.materialName}&materialType=${inforObj.materialType}&inputDay=${inforObj.inputDay}&day=${inforObj.repairDay}&num=${inforObj.num}`;
                // axios.get(url).then(res=>{
                //     console.log(res);
                // })
                // .catch(()=>{
                //     console.log(err);
                // })
            })

        })
    },
    sumInforUpdate(inforObj){
         var url = `/sumInforUpdate?businessValue=${this.businessValue}&materialName=${inforObj.materialName}&materialType=${inforObj.materialType}&inputDay=${inforObj.inputDay}&day=${inforObj.repairDay}&num=${inforObj.num}`;
         axios.get(url).then(res=>{
             console.log(res);
         })
         .catch(()=>{
             console.log(err);
         })
    },
    materialDataInput(inforObj){
        console.log('维修材料录入');
        var url = `/materialInforInput?mainId=${this.mainId}&materialName=${inforObj.materialName}&materialType=${inforObj.materialType}&num=${inforObj.num}&day=${inforObj.repairDay}&inputDay=${inforObj.inputDay}&infor=${inforObj.infor}&address=${inforObj.address}&manName=${inforObj.manName}`;
        axios.get(url).then(res=>{
            console.log(res);
        })
        .catch(()=>{
            console.log(err);
        })
    },
    installDateInput(){
        console.log('提交安装数据');
        this.inforAdded.forEach((inforObj)=>{
            this.installInforInput(inforObj)
        })
    },
    installInforInput(inforObj){
        var url = `/installInforInput?materialName=${inforObj.materialName}&materialType=${inforObj.materialType}&num=${inforObj.num}&day=${inforObj.repairDay}&inputDay=${inforObj.inputDay}&infor=${inforObj.infor}`;
        axios.get(url).then(res=>{
            console.log(res);
        })
        .catch(()=>{
            console.log(err);
        })
    },
    repairDateInput(){
        var url = `/mainInforInput?manName=${this.manName}&address=${this.address[0]+this.address[1]}&inputDay=${this.inputDay}&inputDayString=${this.inputDay}&day=${this.repairDay}&infor=${this.infor}`;
        console.log(url);
        axios.get(url).then(res=>{
            this.mainId = res.data.id;
            this.inforAdded.forEach(inforObj => {
                this.materialDataInput(inforObj)
                this.sumInforUpdate(inforObj)
            });
            this.material=''
            this.num = ''
            this.inforAdded=[]
        })
    },
    getTodayDate(){
        var date = new Date();
        var year = date.getFullYear();
        var month = date.getMonth()+1;
        var day = date.getDate();
        month = month<10?'0'+month:month;
        day = day<10?'0'+day:day;
        var ymd = year+"-"+month+"-"+day;
        return ymd
    },
  },
  computed:{
    dataAdded:{
      get(){
        return this.$store.state.dataAdded
      }
    }
  },
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.form{
    position: relative;
    width: 80%;
    height: 30%;
    margin: 0 auto;
}
.title{
    height: 50px;
    font-family: "curlz mt","华文彩云","arial", "微软雅黑";
    font-size: 25px;
}

.mainInfor-input-line{
    margin-bottom: 30px;
    display: inline-block;
}
.table-material{
    display: none;
}
.infor-title{
    text-align: left;
    font-family: "Times New Roman",Georgia,Serif;
    font-size: 22px;
    margin-bottom: 5px;
    margin-top: 5px;
}
.infor-input-show{
    width: 80%;
    height: 60%;
    margin: 0 auto;
    background-color: whitesmoke;
    border: whitesmoke 10px solid;
    border-radius: 10px;
}
.materila-infor{
    width: 100%;
    height: 250px;
    overflow-y:auto
}
</style>



