<template>
  <div id="home">
    <div>
        <el-menu 
        :default-active="activeIndex" 
        class="el-menu-demo" 
        mode="horizontal" 
        @select="handleSelect"
        background-color='#2292DD'
        text-color='#FFFFFF'
        active-text-color='#CDC9C9'
        >
        <el-menu-item index="inputVue">数据登记</el-menu-item>
        <el-menu-item index="showAllVue">数据查询</el-menu-item>
        <el-menu-item index="summaryTable">报表查询</el-menu-item>
        </el-menu>
    </div>
    <div class="system-title">
    什地水厂材料管理系统
    </div>
  </div>
</template>

<script>
export default {
  name: 'home',
  data() {
      return {
        activeIndex: '1',
      };
    },
    methods: {
      handleSelect(key, keyPath) {
        console.log(key);
        console.log(keyPath);
        this.$router.push(key)
      }
    }
}
</script>

<style>

.system-title{
    font-size: 30px;
    margin: 0 auto;
    position: relative;
    bottom: 50px;
    display: inline;
    color: white;
}

</style>



